<template>
  <div class="home">
    <div class="img">
      <img :src=h_img>
    </div>
    <mt-navbar v-model="selected">
      <mt-tab-item  v-for="(i,index) in list" :key="index" :id="index" @click.native="home_btn(index,i.home_router)" :class="{zcolor:index==z_index}">{{i.name}}</mt-tab-item>
    </mt-navbar>
    <div class="content">
        <div class="item" v-for="(i,index) in a" :key="index">
              <router-link tag="div" :to="{name:'about',params:{id:i.id}}">
                <el-row>
                  <el-col :span="4">
                    <div class="img"><img :src="i.author.avatar_url" style="width:100%"></div>
                  </el-col>
                  <el-col :span="20">
                      <div class="title">{{i.title.length>=20?i.title.slice(0,20)+'...':i.title}}</div>
                      <div style="font-size:12px; color:#ccc"><span>{{i.author.loginname}}</span><span style="float:right;padding-right:50px;">{{changeTime(i.last_reply_at)}}</span></div>
                  </el-col>
                </el-row>
                
              </router-link>
            </div>
        </div>
    </div>
</template>
<script>
import jk from '../assets/jk.png'
export default {
  name: 'home',
  data(){
    return{
      h_img:jk,
      list:[
        {name:'最近回复',home_router:'ask'},
        {name:'最新发布',home_router:'share'},
        {name:'收藏话题',home_router:'job'},
        {name:'收藏话题',home_router:'good'},
      ],
      selected:'',
      z_index:'',
      z_home:'ask',
      a:''
    }
},
  methods:{
    changeTime(date) {
        var newDate = new Date(date).toJSON();
        return new Date(+new Date(newDate) + 8 * 3600 * 1000).toISOString().replace(/T/g, ' ').replace(/\.[\d]{3}Z/, '')
    },
    home_btn(index,z_home){
      this.z_index=index
      this.z_home=z_home
      this.$axios.get('https://cnodejs.org/api/v1/topics', {
					params: {
            page:1,
            tab:this.z_home,
            limit:10,
            mdrender:''
					}
				})
				.then((res)=> {
          var a=res.data.data
          this.a=a
          console.log(this.a)
				})
    },
  },
 mounted(){
   this.$axios.get('https://cnodejs.org/api/v1/topics', {
					params: {
            page:1,
            tab:this.z_home,
            limit:10,
            mdrender:''
					}
				})
				.then((res)=> {
          console.log(res)
          var a=res.data.data
          this.a=a
				})
 }
}
</script>
